.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.login-form {
  width: 100%;
  max-width: 400px;
  position: fixed;
  padding: 30px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.login-form:hover {
  transform: scale(1.02); 
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.3);
}

.login-form h2 {
  margin-bottom: 20px;
  text-align: center;
  font-size: 2rem;
  color: #333;
  font-family: 'Lato', sans-serif;
}

.form-control {
  margin-bottom: 20px;
}

.form-control label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #555;
}

.form-control input {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.form-control input:focus {
  border-color: #007bff;
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.5);
  outline: none;
}

.error-message {
  color: #ff4d4d; 
  font-size: 0.875rem;
  margin-top: 5px;
}

button {
  width: 100%;
  padding: 12px;
  background: linear-gradient(to right, #ff7e5f, #feb47b); 
  border: none;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  border-radius: 8px;
  transition: background 0.3s ease, transform 0.3s ease;
}

button:hover {
  background: linear-gradient(to right, #feb47b, #ff7e5f); 
  transform: scale(1.05);
}

.link-styl {
  text-decoration: none;
  color: #007bff;
  font-weight: 500;
}

.link-styl:hover {
  text-decoration: underline;
  color: #0056b3; 
}
