.medicine-page {
  padding: 20px;
  margin-top: 1300px;
 
  
}

.medicine-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.medicine-header h1{
  font-family: 'Lato', sans-serif;
}
.medicine-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px; 
}

.medicine-card {
  width: calc(20% - 10px);
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 10px;
  text-align: center;
}

.medicine-image {
  width: 100%;
  height: 200px; 
  object-fit: cover;
  margin-bottom: 10px;
}

.medicine-info h2 {
  font-size: 1.2em; 
  margin: 5px 0;
}

.medicine-info p {
  font-size: 0.9em;
  color: #666;
  margin: 5px 0;
}

.medicine-pricing {
  margin: 10px 0;
}

.medicine-price {
  font-size: 1em; /* Reduced font size for compactness */
  color: #333;
}

.order-btn {
  padding: 5px 10px; /* Reduced padding for compactness */
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.order-btn:hover {
  background-color: #0056b3;
}

.show-cart-btn {
  padding: 5px 10px; /* Reduced padding for compactness */
  background-color: #28a745;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 400px;
  margin-right: 650px;
  height: 45px;
}

.show-cart-btn:hover {
  background-color: #218838;
}
.medi_move{
  margin-left: 200px;
}
.medi_hide{
  position: absolute;
  margin-left: 700px;
  padding: 5px 10px; 
  border-radius: 5px;
  cursor: pointer;
  width: 400px;
  
}