.navbar {
  background: linear-gradient(to right, #ff7e5f, #feb47b); 
  padding: 15px 30px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  border-bottom: 2px solid #fff;
  animation: slideInDown 0.5s ease-in-out;
}
@keyframes slideInDown {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

.navbar-logo {
  color: #fff;
  font-size: 28px;
  font-weight: bold;
  text-decoration: none;
  transition: color 0.3s;
  font-family: 'Lato', sans-serif;
  animation: fadeIn 1s ease-in;
}

.navbar-logo:hover {
  color: #0d47a1;
}

.navbar-menu {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.navbar-item {
  margin: 0 20px;
  animation: fadeIn 1s ease-in;
  animation-delay: calc(0.1s * var(--i)); 
}

.navbar-link {
  color: #fff;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-size: 18px;
  font-weight: 500;
  transition: color 0.3s, transform 0.3s;
}

.navbar-link:hover {
  color: #0d47a1;
  transform: translateY(-2px); 
  font-size: 20px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
