.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #f0f0f0;
  color: #333;
  font-family: 'Arial', sans-serif;
  border-radius: 20px;
  width: 100vw;
  height: 100vh;
  animation: fadeIn 2s ease-in-out;
}

.header_hom {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
  max-width: 1200px;
  padding: 2rem;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  text-align: center;
  margin-top: 60px;
  animation: slideDown 1s ease-in-out;
}




.logo {
  width: 45%;
  height: auto;
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70% ;
  box-shadow: 2px 2px 2px 3px rgb(72, 157, 222);
}

.button-container {
  position: absolute;
  top: 1rem;
  right: 1rem;
  display: flex;
  gap: 1rem;
  margin-top: 70px;
  margin-right: 180px;
}

.btn {
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 10px;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.btn-register {
  background: #007bff;
  animation: fadeIn 2s ease-in-out;
}

.btn-login {
  background: #28a745;
  animation: fadeIn 2s ease-in-out;
}

.btn:hover {
  transform: scale(1.05);
}

.btn:focus {
  outline: none;
}

.header_hom h1 {
  font-size: 3.5rem;
  color: #333;
  margin: 0;
  animation: fadeIn 2s ease-in-out;
}

.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
  max-width: 1200px;
  padding: 2rem;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  margin-top: 2rem;
  animation: fadeIn 2s ease-in-out;
}

.intro-section {
  width: 100%;
  margin-bottom: 2rem;
  text-align: center;
  animation: slideUp 1s ease-in-out;
}

@keyframes slideUp {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.features-section {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-bottom: 2rem;
  animation: slideUp 1s ease-in-out;
}

.feature {
  background: #f9f9f9;
  border-radius: 20px;
  padding: 1.5rem;
  width: 30%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.feature:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

.medicines-section {
  width: 100%;
  text-align: center;
  animation: slideUp 1s ease-in-out;
}

.medicines-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
}

.medicines-list li {
  background: #f9f9f9;
  border-radius: 20px;
  padding: 1rem;
  width: 20%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  animation: fadeIn 2s ease-in-out;
}

.contact-section {
  width: 100%;
  text-align: center;
  margin-top: 2rem;
  animation: fadeIn 2s ease-in-out;
}

.contact-icons {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.contact-icons a {
  color: #333;
  transition: color 0.3s ease;
}

.contact-icons a:hover {
  color: #007bff;
}

.footer_hom {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  max-width: 1200px;
  padding: 1rem;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  margin-top: 2rem;
  text-align: center;
  animation: fadeIn 2s ease-in-out;
}

.footer_hom p {
  color: #333;
  margin: 0;
}

.home_title {
  font-family: 'Lato', sans-serif;
}

.hom_about_title {
  font-family: 'Lato', sans-serif;
}

.feature h2 {
  font-family: 'Lato', sans-serif;
}

.medicines-section h2 {
  font-family: 'Lato', sans-serif;
}

.medicines-section strong {
  font-family: 'Lato', sans-serif;
}

.contact-section h2 {
  font-family: 'Lato', sans-serif;
}
 